import actions from './actions';
import mutations from './mutations';
import Logger from '@core/Logger';

const logger = Logger.getLogger('App/Store/Login');

/**
 * 
 */
export default {
    // 
    namespaced: true,
    // 
    state: {
        pending: false
    },
    // state
    mutations: {
        ...mutations(logger)
    },
    // Action  mutation
    actions: {
        ...actions(logger)
    }
};