import Http from '@core/Http';
import { goto } from '@core/router';
import notificator from '@core/notificator';

export default {
    data() {
        return {}
    },
    methods: {
        testDefaultHttpExceptionHandler(type) {
            if (type === 404) {
                Http.getClient().get('/exception_404');
            } else {
                Http.getClient().get('/exception_500');
            }
        },
        testCustomHttpExceptionHandler(type) {
            const customHttpClient = Http.getClient().error(err => {
                if (err.status === 404) {
                    goto('/404');
                } else {
                    notificator.messageError(`${err.status} ${err.statusText}${err.url}`);
                }
                // true, http
                return true;
            });
            if (type === 404) {
                customHttpClient.get('/exception_404');
            } else {
                customHttpClient.get('/exception_500');
            }
        },
        testRESTfulApiMethod(method) {
            switch (method) {
                case 'GET':
                    notificator.alertInfo('GETHTTP1.0todolist ');
                    break;
                case 'POST':
                    notificator.alertInfo('POSTHTTP1.0todolist todo');
                    break;
                case 'PUT':
                    notificator.alertInfo('PUTHTTP1.1/');
                    break;
                case 'PATCH':
                    notificator.alertInfo('PATCHHTTP1.1todolist todo');
                    break;
                case 'DELETE':
                    notificator.alertInfo('DELETEHTTP1.1todolist todo');
                    break;
                case 'HEAD':
                    notificator.alertInfo('HEADHTTP1.0 cache ');
                    break;
                // case 'OPTIONS':
                //     notificator.alertInfo('OPTIONSHTTP1.1 Priflight Request');
                //     break;
                // case 'TRACE':
                //     notificator.alertInfo('TRACEHTTP1.1');
                //     break;
                // case 'CONNECT':
                //     notificator.alertInfo('CONNECTHTTP1.1');
                //     break;
            }
        }
    }
};