import { MUTATIONS } from '../../../constants/storeTypes.js';

export default function(logger) {
    return {
        [MUTATIONS.SET_2_LODING_STATE]: state => {
            state.pending = true;
            logger.debug('  loading');
        },
        [MUTATIONS.SET_2_NORMAL_STATE]: state => {
            state.pending = false;
            logger.debug('  normal');
        }
    }
};