import Http from '@core/Http';
import Logger from '@core/Logger';
import * as LOGIN_API from '../api/login';
import { STATUS_CODE } from '../constants/index.js';
import * as localStorageHelper from '../utils/localStorageHelper';

const logger = Logger.getLogger('App/Router/Interceptor');

// 
const interceptor = {
    // 
    exclude: ['/login', '/404'],
    /**
     * truefalse
     * @param {String} path 
     */
    inFilterChain(path) {
        return !this.exclude.includes(path);
    },
    /**
     * 
     * @param {Object} to 
     * @param {Object} from 
     * @param {Function} next 
     */
    filter(to, from, next) {
        if (this.inFilterChain(to.path)) {
            this.doFilter(next);
        } else {
            next();
        }
    },
    /**
     * 
     * @param {Function} next 
     */
    async doFilter(next) {
        const response = await Http.getClient().headers({ 'token': localStorageHelper.get('token') }).disableLoading().get(LOGIN_API.TOKEN_EXPIRED);
        if (response.code === STATUS_CODE.TOKEN_NOT_EXPIRED) {
            next();
        } else {
            logger.debug('');
            next({ path: '/login' });
        }
    }
};

export default interceptor;