import Vue from 'vue';
import VueRouter from 'vue-router';

// VueRouter
Vue.use(VueRouter);

// 
export let appRouter = null;

/**
 * 
 * @param {Object}     
 */
export const configAppRoutes = (routes = []) => {
    appRouter = new VueRouter({
        routes: [
            ...routes
        ]
    });
    return appRouter;
};