import Vuex from 'vuex';
import Vue from 'vue';
import globalStore from './globalStore';

// Vuex
Vue.use(Vuex);

/**
 * (store)
 * 1state
 * 2getters(state)
 * 3mutations()
 * 4actions()
 * @param {*} appStore 
 */
export const configAppStore = appStore => new Vuex.Store({
    // 
    ...globalStore,
    // 
    modules: {
        ...appStore
    }
});