import Logger from '@core/Logger';
import actions from './actions';
import mutations from './mutations';
import { MODULES, MUTATIONS, ACTIONS } from '../../../constants/storeTypes.js';

const logger = Logger.getLogger('App/Store/Home');

/**
 * (Home)
 */
export default {
    namespaced: true,
    // 
    modules: {
        // 
        [MODULES.COUNTER]: {
            namespaced: true,
            state: {
                count: 0
            },
            getters: {
                countWithRmbPrefix: state => {
                    return `${state.count}`;
                }
            },
            mutations: {
                [MUTATIONS.INCREMENT]: (state, n) => {
                    state.count += n;
                    logger.debug(`: ${n}`);
                },
                [MUTATIONS.DECREMENT]: (state, n) => {
                    state.count -= n;
                    logger.debug(`: ${n}`);
                }
            }
        },
        // todo list
        [MODULES.TODO_LIST]: {
            // home
            namespaced: true,
            state: {
                title: 'my plan',
                todos: []
            },
            // ()
            getters: {
                /**
                 *  mutation  getter
                 * 
                 */
                doneTodos: state => state.todos.filter(todo => todo.done),
                undoneTodos: state => state.todos.filter(todo => !todo.done),
                sortedTodos: (state, getters) => [...getters.undoneTodos, ...getters.doneTodos],
                /**
                 *  getter
                 * 
                 */
                doneTodosCount: (state, getters, rootState) => getters.doneTodos.length,
                undoneTodosCount: (state, getters, rootState) => getters.undoneTodos.length
            },
            /**
             * mutationstore.commit('increment')
             */
            mutations: {
                ...mutations(logger)
            },
            actions: {
                ...actions(logger)
            }
        }
    }
};